  onChangeName(event) {
    let liveStock = { ...this.state.liveStock };
    liveStock[event.target.name] = event.target.value;
    liveStock[event.target.name + "Required"] = false;
    this.setState({ liveStock });
  }
  onSubmit() {
    let liveStock = { ...this.state.liveStock };
    if (liveStock.StockName && liveStock.StockName.trim().length > 0) {
      if (this.state.updateFlag) {
        let liveStockUpdateData = _.pick(liveStock, [
          "Id",
          "StockName",
          "ImageURL",
          "UpdatedBy",
          "UpdatedOn",
          "Active"
        ]);
        liveStock.UpdatedBy = localStorage.getItem("user");
        liveStock.UpdatedOn = new Date();
        liveStockUpdateData.Active = true;
        this.props.updateliveStock(liveStockUpdateData.Id, liveStockUpdateData);
      } else {
        let liveStockData = _.pick(liveStock, [
          "StockName",
          "ImageURL",
          "CreatedBy",
          "CreatedOn",
          "Active"
        ]);
        liveStock.CreatedBy = localStorage.getItem("user");
        liveStock.CreatedOn = new Date();
        liveStockData.Active = true;
        this.props.createliveStock(liveStockData);
      }
      this.setState({ loading: true });
      let message = "";
      let compRef = this;
      setTimeout(() => {
        compRef.props.liveStockError
          ? (message = compRef.props.liveStockError)
          : compRef.state.updateFlag
            ? (message = "Livestock updated successfully")
            : (message = "Livestock created successfully");
        compRef.setState({ loading: false });
        Toaster.Toaster(message, compRef.props.liveStockError);
        setTimeout(() => {
          if (!compRef.props.liveStockError) {
            compRef.onReset();
            compRef.props.history.push("/liveStock/liveStockSteps");
          }
        }, 1000);
      }, 1000);
    } else {
      liveStock.StockNameRequired = true;
      this.setState({ liveStock: liveStock });
    }
  }
  onReset() {
    this.setState({
      liveStock: {
        Id: "",
        StockName: "",
        Active: true,
        CreatedOn: "",
        CreatedBy: "",
        StockNameRequired: false,
        renderURL: ""
      }
    });
  }
